import React from 'react';
import { withRouter } from 'react-router-dom';
import PropTypes from 'prop-types';
// import $ from 'jquery';

const MenuContentsItem = props => (
  <div className="seoulContainer-m popup-box">
    <button
      className="close"
      onClick={() => {
        props.history.goBack();
        // props.rendered(false);
      }}
    >
      &times;
    </button>
    <h1>{props.item.name}</h1>
  </div>
);

MenuContentsItem.propTypes = {
  item: PropTypes.object.isRequired
};

export default withRouter(MenuContentsItem);
